.container {
  padding-left: 32px;
  padding-right: 32px;
  max-width: 1280px;
  margin: 0 auto;
}

@media (min-width: 480px) {
  .container {
    padding-left: 40px;
    padding-right: 40px;
  }
}

@media (min-width: 768px) {
  .container {
    padding-left: 64px;
    padding-right: 64px;
  }
}
